import s from './booking.module.css'

const BookingSelect = () => (
    <>
        <div className={s.topline}></div>
        <form name="user-comfirm" className={s.signform} action="" >

            <div className={s.two_item}>
                <div className={s.sign_item}>
                    <label htmlFor='bookingId' className={s.item_title}></label>
                    <input id='bookingId' className='c-input' type="text" placeholder=' .'/>
                </div>
                <div className={s.sign_item}>
                    <label htmlFor='roomSevice' className={s.item_title}></label> 
                    <input id='roomSevice' className='c-input' type="text" placeholder=' .'/>
                </div>
                
            </div>

            <div className={s.two_item}>
                <div className={s.sign_item}>
                    <label htmlFor='checkIn' className={s.item_title}></label>
                    <input id='checkIn' className='c-input' type="text" placeholder=' .'/>
                </div>
            </div>

            <div className={s.sign_item}>
                <label  className={s.item_title}></label> 
                {/*    <input id='roomSevice' className='c-input' type="text" placeholder=' .'/> */}
            </div>

            <div className={s.notice_text}>*               .   ?</div>
            <button className='c-btn-black'></button>
            
        </form>
    </>
)

export default BookingSelect;       