import NationSelect from '../../../components/nation-selectbox';
import s from './join-member.module.css';
import { FormattedMessage } from 'react-intl';

const Join = () => (
    <>
        <form name="user-comfirm" className={s.signform} action="" >
            <div className={`${s.logo}`}>
                <img src={require("../../../assets/images/logo.png")} alt="logo" />
            </div>
            <div className={s.signbtn_item}>
                <div className={s.sign_item}>
                    <label htmlFor='email' className={s.item_title}><FormattedMessage id='email' /></label>
                    <input id='email' className='c-input' type="text" placeholder=' .' />
                </div>
                <button>
                    <FormattedMessage id='duplicateconfirmation' />
                </button>
                <button>
                    <FormattedMessage id='verificationofauthenticationrequest' />
                </button>
            </div>
            <div className={s.signbtn_item}>
                <div className={s.sign_item}>
                    <input id='code' className='c-input' type="text" placeholder=' .' />
                </div>
                <button> </button>
            </div>
            <div className={s.two_item}>
                <div className={s.sign_item}>
                    <label htmlFor='email' className={s.item_title}></label>
                    <input id='email' className='c-input' type="text" placeholder=' .' />
                </div>
                <div className={s.sign_item}>
                    <label htmlFor='email' className={s.item_title}></label>
                    <input id='email' className='c-input' type="date" placeholder=' .' />
                </div>
            </div>
            <div className={s.sign_item}>
                <label htmlFor='email' className={s.item_title}></label>
                <NationSelect value="aaa"></NationSelect>
            </div>
            <div className={s.sign_item}>
                <label htmlFor='password' className={s.item_title}></label>
                <input id='password' className='c-input' type="text" placeholder=' .' />
            </div>

            <div className={s.signbtn_item}>
                <div className={s.sign_item}>
                    <input id='passwordChk' className='c-input' type="text" placeholder=' .' />
                </div>
                <button>  </button>
            </div>


            <div className={s.btn_wrap}>
                <button className='c-btn-black'></button>
                <button className='c-btn-main'></button>
            </div>
        </form>
    </>
)

export default Join;